import Vue from 'vue'
//引入路由
import Vuerouter from 'vue-router'


//使用路由
Vue.use(Vuerouter);

//引入路由组件
import Main from '../pages/Main'
import Audit from '../pages/Audit'
import Info from '../pages/Information'
import Login from '../pages/Login'
import JDBC from '../pages/Learning/JDBC'
import Mybatis from '../pages/Learning/Mybatis'
import Upload from '../pages/Learning/Upload'
import Traversial from '../pages/Learning/Traversal'
import SSRF from '../pages/Learning/SSRF'
import XEE from '../pages/Learning/XEE'
import RCE from '../pages/Learning/RCE'
import XSS from '../pages/Learning/XSS'
import Reverse from '../pages/Learning/Reverse'
//配置路由
export default new Vuerouter({
    routes: [
        {
            path: '/main',
            component: Main
        },
        {
            path: '/audit',
            component: Audit
        },
        {
            path: '/info',
            component: Info
        },
        {
            path: '/login',
            component: Login
        },
        {
            path: '/learning/JDBC',
            name: 'JDBC',
            component: JDBC,
        },
        {
            path: '/Learning/Mybatis',
            name: 'Mybatis',
            component: Mybatis,
        },
        {
            path: '/Learning/traversial',
            name: 'Traversial',
            component: Traversial,
        },
        {
            path: '/Learning/upload',
            name: 'Upload',
            component: Upload,
        },
        {
            path: '/learning/ssrf',
            name: 'SSRF',
            component: SSRF,
        },
        {
            path: '/learning/rce',
            name: 'RCE',
            component: RCE,
        },
        {
            path: '/learning/xee',
            name: 'XEE',
            component: XEE,
        },
        {
            path: '/learning/xss',
            name: 'XSS',
            component: XSS,
        },
        {
            path: '/learning/reverse',
            name: 'reverse',
            component: Reverse,
        },
        {
            path: '*',
            redirect: '/main'
        }
    ],
})